import React from 'react'
import Slider from "react-slick/lib";

export default class Awards extends React.Component {

  render() {
    let sliderOption = {
      adaptiveHeight: false,
      autoplay: true,
      speed: 700,
      dots: false,
      arrows: true,
      slidesToShow: 3,
      slidesToScroll: 1,
      responsive: [
        {
          breakpoint: 1024,
          settings: {
            slidesToScroll: 1,
            slidesToShow: 1,
          }
        }
      ]
    };
    return (
      <Slider className="skill__slider" {...sliderOption}>
        <div className="skill__cell">
          <div className="skill__item">
            <img src="../img/skill_item_01.jpg" alt="Почетная Грамота"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src="../img/skill_pict.png" alt="Медаль" className="no-shadow"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src="../img/skill_item_02.jpg" alt="Почетная Грамота"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src="../img/skill_item_03.jpg" alt="Почетная Грамота"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src="../img/skill_item_04.jpg" alt="Почетная Грамота"/>
          </div>
        </div>
        <div className="skill__cell">
          <div className="skill__item">
            <img src="../img/skill_item_05.jpg" alt="Почетная Грамота"/>
          </div>
        </div>
      </Slider>
    )
  }

}